@media (max-width: 1600.98px) {
  .container {
    grid-column-gap: 0;
    padding: 0 30px;
    box-sizing: border-box; }
  .footer__adress {
    grid-column: 10/13; }
  .footer__email {
    grid-column: 8/10; } }

@media (max-width: 1366.98px) {
  .slider__dots {
    width: 920px; }
  .nav {
    grid-column: 5 / 10; }
  .sidebar__contacts {
    flex-wrap: wrap; }
  .sidebar__phone {
    width: 100%;
    margin-bottom: 20px; }
  .sidebar.in-fix {
    max-width: 30%; }
  .page-header .nav {
    grid-column: 8/13; }
  .banner__txt {
    grid-column: 3/9; }
  .slider__counter {
    top: 25%; } }

@media (max-width: 1280.98px) {
  .container {
    grid-template-columns: repeat(auto-fill, minmax(100px, 1fr)); }
  .sidebar {
    box-sizing: border-box; } }

@media (max-width: 1025.98px) {
  .sidebar {
    box-sizing: border-box;
    width: 341px; }
  .sticky-wrapper.is-sticky .sidebar {
    box-sizing: revert; }
  .container {
    grid-template-columns: repeat(auto-fill, minmax(80px, 1fr)); }
  .nav {
    grid-column: 5 / 13; }
  .top-phone {
    grid-column: 10/13; }
  .banner__ttl {
    font-size: 35px; }
  .slider__number {
    font-size: 150px; }
  .slider__counter {
    top: 40%; }
  .slider__number:after {
    width: 155px;
    height: 100px; }
  .slider__dots {
    width: 665px; }
  .page-header .nav {
    grid-column: 6/13; }
  .case__wrapper {
    flex-wrap: wrap; }
  .case__item {
    flex: 0 0 50%;
    max-width: 50%; }
  .sidebar__nav_link {
    font-size: 15px; }
  .footer__item p:last-child {
    line-height: 30px; }
  .case-in__info {
    max-width: 100%; }
  .cases2__info {
    max-width: 100%; } }

@media (max-width: 768.98px) {
  body {
    overflow-x: hidden; }
  .nav_top {
    display: none; }
  .nav_mobile {
    display: block; }
  .top-phone {
    grid-column: 6/10; }
  .hamburger {
    display: block; }
  .menu {
    right: -120%;
    width: 100%;
    min-height: 400px;
    height: 100vh;
    overflow-y: scroll;
    box-sizing: border-box;
    display: flex; }
  .sidebar__social_item_in {
    margin-right: 0; }
  .slider__dots {
    width: 465px; }
  .banner__ttl {
    font-size: 28px; }
  .banner__subttl {
    font-size: 18px; }
  .slider__number {
    font-size: 100px; }
    .slider__number:after {
      width: 170px;
      height: 80px; }
  .slider__count {
    font-size: 40px; }
  .sidebar {
    grid-column: 1/13; }
    .sidebar__nav_wr_top {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap; }
    .sidebar__nav_item {
      width: 45%; }
    .sidebar__nav_link {
      font-size: 16px; }
    .sidebar__contacts {
      display: none; }
  .content {
    grid-column: 1/13;
    margin-top: 50px; }
  .footer__copy {
    grid-column: 1/5; }
  .footer__email {
    grid-column: 5/8; }
  .footer__adress {
    grid-column: 1/13;
    margin-top: 20px; }
  .page-header .top-phone {
    display: block;
    color: #262626; }
  .page-header .hamburger:after, .page-header .hamburger:before {
    background-color: #262626; } }

@media (max-width: 480.98px) {
  .logo {
    grid-column: 1/2; }
    .logo__caption {
      display: none; }
  .top-phone {
    grid-column: 2/10;
    font-size: 20px; }
  .slider__counter {
    display: none; }
  .banner__txt {
    grid-column: 1/13; }
  .slider__dots {
    width: 90%;
    bottom: 30%; }
  .language__link {
    font-size: 15px; }
  .mouse__caption {
    transform: rotate(0);
    position: relative;
    top: 0;
    left: 0;
    font-size: 16px;
    text-decoration: underline; }
  .mouse__icon {
    display: none; }
  .sidebar__nav_item {
    width: 100%; }
  .only-mobile {
    display: block; }
  .content__in_design {
    background: none !important; }
  .content__img {
    margin-bottom: 20px; }
  .content__in {
    margin-bottom: 0; }
  .content__flex {
    margin-right: 0; }
  .content__in_design_left .title {
    text-align: left; }
  .case {
    margin-top: 50px; }
    .case__item {
      flex: 0 0 100%;
      max-width: 100%;
      margin-bottom: 30px; }
  .footer {
    margin-top: 0; }
    .footer__email {
      grid-column: 1/13;
      margin-top: 20px; }
    .footer__copy {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-around;
      align-items: center;
      border-bottom: 1px solid #1B1B1B;
      padding-bottom: 20px; }
      .footer__copy p {
        margin: 0; }
    .footer__adress {
      border-bottom: 1px solid #1B1B1B;
      padding-bottom: 20px; }
    .footer__phone {
      display: flex;
      justify-content: space-between;
      align-items: center;
      grid-column: 1/13;
      margin-top: 30px; }
      .footer__phone a {
        font-size: 18px;
        font-family: 'mont-bold', serif; }
  .breadcrumbs {
    grid-column: 1/13; }
  .case-in__wr {
    padding: 30px; }
  .cases2__item {
    padding: 30px; } }

@media (max-width: 375.98px) {
  .top-phone {
    font-size: 16px; }
  .slider__dots {
    bottom: 25%; }
  .title {
    font-size: 25px; }
  .content__in .title {
    font-size: 20px; } }

@media (max-width: 320.98px) {
  .logo svg {
    width: 42px;
    height: 62px; }
  .top-phone {
    grid-column: 3/11; }
  .footer__phone a {
    font-size: 16px; }
  .sidebar__social_item:first-child {
    margin-right: 15px; }
  .footer__copy {
    align-items: flex-start; }
  .footer__copy_wr {
    max-width: 70%; } }
